.heading{
    position: relative;
}
.heading:after{
    content: "";
    position: absolute;
    top: 180%;
    left: 0;
    width: 50%;
    height: 3px;
    background-color: #19ABA7;
}
.book{
    position: absolute;
    top: 0;
    right: 10%;
}
@media all and (min-width: 320px) and (max-width: 768px){
    .book{
        display: none!important;
    }
    .query{
        display: block!important;
    }
}